    /* Add media query for smaller screens */
    @media only screen and (max-width: 768px) {
        .containerbg {
            flex-direction: column;
            align-items: center; 
        }
    
        .address-list {
            flex-direction: column;
            padding: 20px;}
    
        .address {
            text-align: center; 
            margin-bottom: 20px;
        }
    
        .address h2{
    font-size: medium;
    /* margin-left: auto; */
        }
        .address ul li{
    
            font-size: medium;
    /* margin-left: auto; */
    /* margin-right: 10px; */
        }
    
        .address-links {
            text-align: center; 
            margin-top: 20px; 
        }
    
        .listt {
            justify-content: center; 
        }
    
        .iconn {
            font-size: 36px;
            color: aliceblue;
            transition: color 0.3s ease;
            }
            
            .iconn:hover {
            color: brown;
            }
        
      
        .hr {
            width: 65%; 
            margin: 0%;
            margin-left: 80px;
            height: 3px;
        }
    }
    @media only screen and (max-width: 768px) {
    
    .fivelinks {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        padding: 0px 20px; 
    }
    
    .item1,
    .item2,
    .item3,
    .item4,
    .item5 {
        width: 100%; 
        margin-bottom: 20px; 
    }
    
    .sub-links {
        display: block; 
        text-align: center; 
        margin-top: 10px; 
        color: #fff;
    }
    
    .fivelinks h4{
    text-align: center;
    
    }
    
    
    }